.ShopToken_row_container:hover{
    height: 200px;
}

.ShopToken_row_container{
    width: 870px;
    height: 105px;
    float: left;
    overflow: hidden;
}

.ShopToken_product_tool{
    float: right;
    width: 70px;
}

.ShopToken_row{
    height: 100%;
    width: 800px;
    margin-bottom: 10px;
}

.ShopToken_row:hover{
 height: 105px;
    width: 800px;
    margin-bottom: 10px;
}

.ShopToken_item_icon{
    float: left;
    width: 50px;
    cursor:pointer;
    margin-top: 5px;
}


.ShopToken_item_label{
    float: left;
    margin-top: 0px;
    margin-left: 10px;
}

.ShopToken_item_price_icon{
    float: right;
    width: 50px;
}

.ShopToken_item_price{
    margin-right: 10px;
}

.ShopToken_item_setprice{
    width: calc( 100% - 10px );
    height: 60px;
    background: #67bfd9;
    border-radius: 10px;
    margin-top: 20px;
    padding: 5px;
}


.ShopToken_item_setprice_field{
    font-size: 32px;
    color: white;
    background: #67bfd9;
    border: solid 0px;
    margin-left: 5px;
    width: 490px;
    margin-top: 5px;
    padding: 7px;
    padding-left: 10px;
}

.ShopToken_item_setprice_button{
    cursor: pointer;
    margin: auto;
    padding: 5px;
    font-size: 32px;
    color: white;
    background: #0788ae;
    border-radius: 7px;
    padding-right: 20px;
    padding-left: 15px;
    float: right;
}

.ShopToken_item_setprice_button:hover{
background: white;
color: green;
}

.ShopToken_item_setamount_field{
    font-size: 32px;  
    color: white;
    background: #67bfd9;
    border: solid 0px;
    margin-left: 5px;
    width: 180px;
    float: left;
    margin-top: 7px;
    text-align: right;
    padding-right: 10px;
}