
.Price_list_label{
color: white;
font-size: 40px;
margin-bottom: 30px;
text-align: left;
}

.PriceList_empty_message{
color: white;
font-size: 40px;
margin-bottom: 20px;
text-align: center;
width: 100%;
margin-top: 20px;
}

.PriceList_module_link_icon{
    width: 50px;
    margin:auto;
    cursor: pointer;
    margin-left: 20px;
    margin-bottom: 30px;
}

.PriceList_box_link_holder{
    float: right;
    width: 60px;
}

.PriceList_box_link_holder:hover{
    width: 150px;
}

.PriceList_module_main{
    margin-bottom: 50px;
}

.PriceList_ticket_link{
    text-decoration: none;
    width: 60px;
    float: left;
}

.PriceList_module_item_row{
    margin:auto;
    width: 800px;
    margin-bottom: 10px;
}

.PriceList_add_item_panel{
overflow: hidden;
font-size: 40px;
color: white;
height: 50px;
padding: 20px;
background:  #0788ae;
border: solid 3px #0788ae;
border-radius: 20px;
text-align: center;
}

.PriceList_add_item_panel:hover{
    border: solid 3px white;
    height: 245px;
}

.PriceList_module_link_panel{
overflow: hidden;
font-size: 40px;
color: white;
height: 50px;
padding: 20px;
background:  #0788ae;
border: solid 3px #0788ae;
border-radius: 20px;
}

.PriceList_module_link_panel:hover{
border: solid 3px white;
}


.PriceList_item_setprice{
    width: calc( 100% - 10px );
    height: 60px;
    background: #67bfd9;
    border-radius: 10px;
    margin-top: 20px;
    padding: 5px;
}

.PriceList_item_setprice_button{
    cursor: pointer;
    margin: auto;
    padding: 5px;
    font-size: 32px;
    color: white;
    background: #0788ae;
    border-radius: 7px;
    padding-right: 20px;
    padding-left: 15px;
    float: right;
    margin-top: 2px;
}

.PriceList_item_setprice_button:hover{
background: white;
color: green;
}

.PriceList_add_item_setprice_field{
    font-size: 32px;
    color: white;
    background: #67bfd9;
    border: solid 0px;
    margin-left: 5px;
    width: 490px;
    padding: 7px;
    padding-left: 10px;
    float: left;
    margin-top: 5px;
}

#PriceList_add_item_setaddress_field{
    font-size: 32px;
    color: white;
    background: #67bfd9;
    border: solid 0px;
    margin-left: 5px;
    width: 490px;
    padding: 7px;
    padding-left: 10px;
    float: left;
    margin-top: 5px;
}

#PriceList_add_item_icon{
    width: 50px;
    margin:auto;
}

.PriceList_label{
    color: white;
    font-size: 50px;
    float: left;
    width: calc( 100% - 40px );
    padding: 20px;
    border-radius: 15px;
    cursor: pointer;
}

.right{float: right;}

.paddingright{padding-right: 20px;}
   
.PriceList_item_setprice_field{
    font-size: 32px;
    color: white;
    background: #67bfd9;
    border: solid 0px;
    margin-left: 5px;
    width: 490px;
    margin-top: 5px;
    padding: 7px;
    padding-left: 10px;
}

.clear{clear:both;}
