.TicketInstaller_Modules_Installer{
    overflow: hidden;
    font-size: 40px;
    color: white;
    padding: 10px;
    background:  #0788ae;
    border: solid 3px #0788ae;
    border-radius: 10px;
    width: calc( 100% - 26px );
    margin-top: 10px;
    height: 55px;
    text-align: left;
}
    
    
    
.TicketInstaller_Modules_Installer_disabled{
    overflow: hidden;
    font-size: 40px;
    color: white;
    padding: 10px;
    background:  #0788ae;
    border: solid 3px #0788ae;
    border-radius: 10px;
    width: calc( 100% - 26px );
    margin-top: 10px;
    height: 55px;
    text-align: left;
    opacity: 0.5;
}

.TicketInstaller_Modules_Installer:hover{
    border: solid 3px white;
    height: 170px;
}
    
.TicketInstaller_Modules_Installer_disabled:hover{
    border: solid 3px white;
    height: 170px;
}


.TicketInstaller_module_icon{
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
}

.TicketInstaller_desc{

        margin-left: 60px;
        font-style: italic;
        width: 100%;
        margin-top: 10px;
        margin-bottom: 10px;
}

.TicketInstaller_module_install_button{
    opacity: 1;
    cursor: pointer;
    margin: auto;
    padding: 5px;
    font-size: 32px;
    color: white;
    background:  #67bfd9;
    border-radius: 7px;
    padding-right: 20px;
    padding-left: 15px;
    float: right;
}

.TicketInstaller_module_install_button:hover{
background: white;
color: green;
opacity: 1;
}