
.Ticket_Modules{
    cursor: pointer;
    overflow: hidden;
    font-size: 40px;
    color: white;
    padding: 10px;
    background:  #0788ae;
    border: solid 3px #0788ae;
    border-radius: 10px;
    width: calc( 100% - 26px );
    margin-top: 5px;
    height: 54px;
}
    
.Ticket_Modules_disabled{
    overflow: hidden;
    font-size: 40px;
    color: white;
    padding: 10px;
    background:  #0788ae;
    border: solid 3px #0788ae;
    border-radius: 10px;
    width: calc( 100% - 26px );
    margin-top: 5px;
    height: 54px;
    opacity: 0.5;
}
   
.Ticket_Modules:hover{
    border: solid 3px white;
    height: 170px;
}
 
    
.Ticket_Modules_disabled:hover{
    border: solid 3px white;
    height: 170px;
}
    
.Ticket_desc{
    margin-left: 60px;
    font-style: italic;
    width: 100%;
}

.Ticket_module_icon{
    float: left;
    margin-right: 10px;
    margin-bottom: 10px;
}
    
.Ticket_module_install_button{
        opacity: 1;
        cursor: pointer;
        margin: auto;
        padding: 5px;
        font-size: 32px;
        color: white;
        background:  #67bfd9;
        border-radius: 7px;
        padding-right: 20px;
        padding-left: 15px;
        float: right;
}
    
.Ticket_module_install_button:hover{
    background: white;
    color: green;
    opacity: 1;
}